import React, {
  PropTypes,
  Component
} from 'react';
import ReactDOM from 'react-dom';

import {
  withGoogleMap,
  GoogleMap,
} from "react-google-maps";

import Config from '../../config';
import './Map.css';
import MapStyles from './MapStyles.json';

const MapElement = withGoogleMap(props => (
  <GoogleMap ref={props.onMapMounted}
             defaultZoom={13}
             defaultCenter={props.defaultCenter}
             defaultOptions={{ styles: MapStyles }}
             onBoundsChanged={props.boundsChanged}>
  </GoogleMap>
));

class Map extends Component {
  static propTypes = {
    onBoundsChanged: PropTypes.func,
    printSize: PropTypes.object
  }

  constructor() {
    super();
    this.defaultCenter = {
      lat: 52.3557194,
      lng: 4.8889233
    };
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((res) => {
        this.defaultCenter = {
          lat: res.coords.latitude,
          lng: res.coords.longitude
        };
      });
    }
  }

  componentDidUpdate(prevProps) {
    if(this.props.printSize === {}) return;

    let el = ReactDOM.findDOMNode(this);
    let ratio = this.props.printSize.width / this.props.printSize.height;
    let height = el.getBoundingClientRect().width * ratio;
    el.style.height = `${height}px`;

    // TODO: see this: https://github.com/tomchentw/react-google-maps/issues/337.
    // As far as I know this hasn't been solved in a clean way yet.
    window.google.maps.event.trigger(this._map.context['__SECRET_MAP_DO_NOT_USE_OR_YOU_WILL_BE_FIRED'], "resize");
  }

  render() {
    return (
      <div className="Map">
        <div className="widthIndicator">
          <div className="text">
            <span>{this.props.printSize.width} cm</span>
          </div>
        </div>
        <div className="heightIndicator">
          <div className="text">
            <span>{this.props.printSize.width} cm</span>
          </div>
        </div>
        <MapElement containerElement={<div style={{ height: `100%` }} />}
                    mapElement={<div style={{ height: `100%` }} />}
                    defaultCenter={this.defaultCenter}
                    onMapMounted={(map) => { this._map = map; }}
                    onSearchBoxMounted={(searchBox) => { this._searchBox = searchBox; }}
                    boundsChanged={() => { this.props.onBoundsChanged(this._map); }} />
      </div>
    );
  }
}

export default Map;



